import { Link } from 'react-router-dom';
import LogoIms from '@assets/logo-ims.svg?react';
import ArrowBottomFill from '@assets/arrow-bottom-fill.svg?react';
import TextButton from '../Button/TextButton';
import { useState, useEffect } from 'react';
import { ChevronDownIcon } from '@heroicons/react/24/solid';

type TNavLink = {
  name: string;
  url?: string;
  subNav?: TSubNav[];
};

type TSubNav = {
  heading: string;
  items: Array<{
    name: string;
    url: string;
  }>;
};

const navList: TNavLink[] = [
  {
    name: 'About IMS',
    url: '#about',
  },

  {
    name: 'Featured campaigns',
    url: '/featured-campaigns',
  },
  {
    name: 'Our news',
    url: '/our-news',
  },
  {
    name: 'Contact us',
    url: '#footer',
  },
];

const SubNav = ({ items }: { items: TSubNav[] }) => {
  return (
    <div className="invisible absolute left-0 top-full flex w-full shadow-lg transition-all hover:visible peer-hover:visible">
      {items?.map(({ heading, items }) => (
        <div key={heading} className="flex-1 px-10 py-10">
          <h4 className="text-currentColor font-medium">{heading}</h4>
          <ul className="mt-4 flex flex-col gap-4">
            {items.map(({ name, url }) => (
              <li key={name} className="text-sm">
                <Link to={url}>{name}</Link>
              </li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  );
};

const Nav = () => {
  const [isMobile, setIsMobile] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  useEffect(() => {
    const mediaQuery = window.matchMedia('(max-width: 768px)');
    setIsMobile(mediaQuery.matches);

    const handler = (event: MediaQueryListEvent) => setIsMobile(event.matches);
    mediaQuery.addEventListener('change', handler);

    return () => mediaQuery.removeEventListener('change', handler);
  }, [isMobileMenuOpen]);

  const handleOptionClick = (
    navLinkUrl: string | undefined | null,
    e: React.MouseEvent,
  ) => {
    e.stopPropagation();
    setIsMobileMenuOpen(false);
    if (navLinkUrl) {
      window.location.href = navLinkUrl;
    }
  };

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };
  const handleMouseLeave = (event: React.MouseEvent<HTMLUListElement>) => {
    if (event.currentTarget === event.relatedTarget) {
      return;
    }
    setIsMobileMenuOpen(false);
  };

  return (
    <nav className="relative" onMouseLeave={handleMouseLeave}>
      {isMobile && (
        <button
          className="z-30 py-6 text-gray-700 md:hidden"
          onClick={toggleMobileMenu}
        >
          <svg
            className="h-6 w-6 fill-current"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            {isMobileMenuOpen ? (
              "hidden"
            ) : (
              <path d="M3 18h18v-2H3v2zm0-5h18v-2H3v2zm0-7v2h18V6H3z" />
            )}
          </svg>
        </button>
      )}
      {/* Mobile Menu (Hidden on Desktop) */}
      {isMobileMenuOpen && (
        <div className="fixed left-0 top-0 z-10 flex h-full w-full flex-col justify-center overflow-y-auto bg-gradient-to-t from-sky-200 via-rose-100 to-amber-50 text-center">
          <button
            className="absolute right-4 top-4 rounded-md p-2 text-gray-700 hover:bg-gray-200 focus:outline-none focus:ring-2 focus:ring-gray-500"
            onClick={toggleMobileMenu}
          >
            <svg
              className="h-6 w-6 fill-current"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z" />
            </svg>
          </button>
          <ul className="p-4 text-center">
            {navList.map((navLink) => (
              <li
                key={navLink.name}
                className="mb-5 flex w-full items-center justify-center"
                onClick={(e) => handleOptionClick(navLink.url, e)}
              >
                {navLink.url ? (
                  <Link
                    to={navLink.url}
                    className="text-sm font-semibold text-gray-700"
                  >
                    {navLink.name}
                  </Link>
                ) : (
                  <div className="w-full px-6 py-4 text-center">
                    <div
                      className="flex items-center justify-between"
                      onClick={(e) => e.stopPropagation()}
                    >
                      <p className="text-sm font-semibold text-gray-700">
                        {navLink.name}
                      </p>
                      <ChevronDownIcon className="h-5 w-5 text-gray-500" />
                    </div>
                    {navLink.subNav && <SubNav items={navLink.subNav} />}
                  </div>
                )}
              </li>
            ))}
          </ul>
        </div>
      )}

      {/* Main Navigation (Hidden on Mobile) */}
      <ul
        className={`hidden ${isMobile ? 'hidden' : 'md:flex'} text-currentColor gap-8`}
      >
        {navList.map((navLink) =>
          navLink.url ? (
            <a href={navLink.url} className="py-6" key={navLink.name}>
              <TextButton whileHover={'animate'}>{navLink.name}</TextButton>
            </a>
          ) : (
            <div key={navLink.name} className="relative">
              <div className="peer flex cursor-pointer items-center gap-2 py-6">
                <TextButton whileHover={'animate'}>{navLink.name}</TextButton>
                <ArrowBottomFill />
              </div>
              {navLink.subNav && <SubNav items={navLink.subNav} />}
            </div>
          ),
        )}
      </ul>
    </nav>
  );
};

function Header({ Logo }: { Logo?: React.ReactNode }) {
  // TODO: change background color
  return (
    <header className="mx-auto flex w-full max-w-screen-2xl items-center justify-between bg-transparent px-10 text-inherit">
      <Link to="/">
        <div className="">
          {Logo ? Logo : <LogoIms className="h-12 w-auto" />}
        </div>
      </Link>
      <Nav />
    </header>
  );
}

export default Header;
