// eslint-disable-next-line @typescript-eslint/no-unused-vars
import styles from './app.module.css';
import MainLayout from './components/layout/MainLayout';
import {
  Services,
  OurWork,
  AboutUs,
  Contact,
  Careers,
  News,
  FeaturedCampaigns,
  Home,
} from './components/features';
import { Routes, Route } from 'react-router-dom';
import gsap from 'gsap';
import { useGSAP } from '@gsap/react';
import { ScrollToPlugin, ScrollTrigger } from 'gsap/all';
import BackToTopButton from './components/BackToTheTopButton/backToTopButton';

gsap.registerPlugin(ScrollToPlugin, ScrollTrigger, useGSAP);

export function App() {
  return (
    <div className={styles.mainLayoutContainer}>
      {/* <MainLayout /> */}
      <Routes>
        <Route path="/" element={<Home />} />
        <Route
          path="/services"
          element={
            <MainLayout>
              <Services />
            </MainLayout>
          }
        />
        <Route
          path="/about"
          element={
            <MainLayout>
              <AboutUs />
            </MainLayout>
          }
        />
        <Route
          path="/our-work"
          element={
            <MainLayout>
              <OurWork />
            </MainLayout>
          }
        />
        <Route
          path="/contact"
          element={
            <MainLayout>
              <Contact />
            </MainLayout>
          }
        />
        <Route
          path="/careers"
          element={
            <MainLayout>
              <Careers />
            </MainLayout>
          }
        />
        <Route
          path="/news"
          element={
            <MainLayout>
              <News />
            </MainLayout>
          }
        />
        <Route
          path="/featured-campaigns"
          element={
            <MainLayout>
              <FeaturedCampaigns />
            </MainLayout>
          }
        />
      </Routes>
      <BackToTopButton />
      {/* </MainLayout> */}
    </div>
  );
}

export default App;
