import { MotionProps, motion } from 'framer-motion';

interface ITextButton extends MotionProps {
  children: string;
  className?: string;
}

const TextButton = ({ children, className, ...props }: ITextButton) => {
  return (
    <motion.button className={className} {...props}>
      <div className="relative flex flex-col items-center justify-center overflow-hidden whitespace-nowrap leading-normal">
        <motion.span
          variants={{
            animate: { y: '-100%', transition: { duration: 0.3 } },
          }}
          className={`after:absolute after:left-0 after:top-full after:w-full after:content-[attr(data-after)]`}
          data-after={children}
        >
          {children}
        </motion.span>
      </div>
    </motion.button>
  );
};

export default TextButton;
