import React, { useEffect, useState, useRef } from 'react';
import { nanoid } from 'nanoid';
import Ripple from '../MagicUI/Ripple';
import FlipText from '../MagicUI/FlipText';
import { useMediaQuery } from 'react-responsive';

interface ListItemProps {
  title: string;
  items: string[];
}

const ListItem: React.FC<ListItemProps> = ({ title, items }) => {
  const [isHovered, setIsHovered] = useState(false);
  const [isClicked, setIsClicked] = useState(false);
  const isMobile = useMediaQuery({ maxWidth: 767 });
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const contentRef = useRef<HTMLDivElement>(null);
  const [contentHeight, setContentHeight] = useState<string>('0px');

  useEffect(() => {
    if (contentRef.current) {
      setContentHeight(isOpen ? `${contentRef.current.scrollHeight}px` : '0px');
    }
  }, [isOpen]);

  const menuRef = useRef<HTMLDivElement>(null);

  const handleMouseEnter = () => {
    setIsHovered(true);
    if (menuRef.current) {
      menuRef.current.style.transitionDelay = '0ms';
    }
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
    setIsClicked(false);
    if (menuRef.current) {
      menuRef.current.style.transitionDelay = '300ms';
    }
  };

  const handleMouseClick = () => {
    setIsClicked(!isClicked);
  };

  return (
    <div
      className="group relative mb-1 md:mb-2 overflow-hidden py-4 text-black cursor-pointer"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      onFocus={handleMouseEnter}
      onBlur={handleMouseLeave}
      onClick={handleMouseClick}
      aria-expanded={!isMobile ? isHovered : isMenuOpen}
      aria-haspopup="true"
    >
      {/* menu item background */}
      <div
        className={`absolute left-0 top-0 h-full w-0 rounded-full bg-primary transition-all duration-300 ease-out group-hover:w-[calc(100%-16px)] ${isMobile ? 'hidden' : ''}`}
      >
        <div
          className={`bg-background absolute right-0 flex h-full w-full max-w-[40rem] items-center justify-center overflow-hidden rounded-full object-contain ${isClicked ? 'opacity-100' : 'opacity-0'}`}
        >
          <Ripple />
        </div>
      </div>
      <div className={`${isMobile ? 'hidden' : ''}`}>
        <div
          className={`relative z-10 p-6 pr-40 transition-colors duration-300 ease-in-out group-hover:text-white`}
        >
          <h3 className="text-2xl font-medium">{title}</h3>
        </div>
        <div
          ref={menuRef}
          className={`absolute bottom-2 right-16 top-5 grid w-fit grid-cols-2 items-start justify-center gap-x-10 transition-opacity duration-300 ease-in-out ${isClicked ? 'opacity-100' : 'opacity-0'} `}
          onClick={handleMouseClick}
        >
          {items.map((item, index) => (
            <div key={nanoid()}>
              <FlipText
                className="text-sm tracking-[-0.1em] text-white"
                word={item}
              />
            </div>
          ))}
        </div>
      </div>

      {isMobile ? (
        <div className="relative">
          <div className="mt-0 rounded-xl bg-primary p-4">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className={`flex w-full items-center justify-between text-left ${isOpen ? 'text-white' : 'text-black'}`}
            >
              <h2 className="text-lg font-bold">{title}</h2>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className={`h-6 w-6 transform transition-transform ${isOpen ? 'rotate-180' : ''}`}
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M19 9l-7 7-7-7"
                />
              </svg>
            </button>
            <div
              ref={contentRef}
              style={{ height: contentHeight }}
              className={`overflow-hidden transition-all duration-300 ease-in-out ${isOpen ? 'mt-2' : ''}`}
            >
              {items.map((item) => (
                <div key={nanoid()} className="mt-2">
                  <FlipText
                    className="text-sm tracking-[-0.1em] text-white"
                    word={item}
                  />
                </div>
              ))}
            </div>
          </div>
        </div>
      ) : (
        <div
          ref={menuRef}
          className={`absolute bottom-2 right-16 top-5 grid w-fit grid-cols-2 items-start justify-center gap-x-10 transition-all duration-300 ${isClicked ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0'}`}
          onClick={handleMouseClick}
        ></div>
      )}
    </div>
  );
};

export default ListItem;
